import React, { useState, useRef, useEffect } from "react";
import "./home.css";
import { makeStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import MyDialog from "../Components/dialog";
import Data from "../Data/Data";
import Random from "./random/Random";
import tenet from "../posters/tenet.jpg";

function Home() {
    const classes = useStyles();
    const [value, setValue] = useState(0);
    const [isMobile, setIsMobile] = useState(true);
    const [open, setOpen] = useState(false);
    const [scroll, setScroll] = useState("paper");
    const [movieData, setMovieData] = useState({});
    const descriptionElementRef = useRef(null);

    const handleClickOpen = (scrollType, movie) => () => {
        setOpen(true);
        setScroll(scrollType);
        setMovieData(movie);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleIsMobile = () => {
        if (window.innerWidth <= 540) setIsMobile(false);
        else setIsMobile(true);
    };

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    useEffect(() => {
        handleIsMobile();

        //-------------------
        if (open) {
            const { current: descriptionElement } = descriptionElementRef;
            if (descriptionElement !== null) {
                descriptionElement.focus();
            }
        }
    }, [open]);

    window.addEventListener("resize", handleIsMobile);
    return (
        <>
            <div className={classes.fadeIn}>
                <div className="root-container">
                    <div className="app-container ">
                        <div className="week-movie-container">
                            <div className="title-wrapper">
                                <h1> + This Week Movie +</h1>
                                <h2>(Tenet)</h2>
                            </div>
                            <div className="week-movie-wrapper">
                                <img loading="lazy" src={tenet} alt="" />
                            </div>
                        </div>
                        <div className="random-comp">
                            <Random />
                        </div>

                        <div className={classes.root}>
                            <Tabs
                                orientation="vertical"
                                variant="scrollable"
                                value={value}
                                onChange={handleChange}
                                aria-label="Vertical tabs example"
                                className={classes.tabs}
                            >
                                {Data.map((data) => {
                                    return (
                                        <Tab
                                            key={data.id}
                                            label={` ${data.name} `}
                                            {...a11yProps(data.id)}
                                        />
                                    );
                                })}
                            </Tabs>

                            {Data.map((data) => {
                                return (
                                    <div key={data.id}>
                                        <TabPanel value={value} index={data.id}>
                                            {data.movies.map((e) => {
                                                return (
                                                    <div
                                                        key={e.id}
                                                        className="movie-container"
                                                    >
                                                        <MyDialog
                                                            open={open}
                                                            scroll={scroll}
                                                            handleClose={
                                                                handleClose
                                                            }
                                                            descriptionElementRef={
                                                                descriptionElementRef
                                                            }
                                                            movie={movieData}
                                                        />

                                                        <div className="movie-name-wrapper">
                                                            <button
                                                                className="movie-name-btn"
                                                                onClick={handleClickOpen(
                                                                    "paper",
                                                                    e
                                                                )}
                                                            >
                                                                {e.name}
                                                            </button>
                                                        </div>

                                                        {isMobile && (
                                                            <div className="movie-year-wrapper">
                                                                {` | ${e.year} |`}
                                                            </div>
                                                        )}

                                                        {isMobile && (
                                                            <div className="imdb-wrapper">
                                                                {e.imdb}
                                                            </div>
                                                        )}

                                                        <div
                                                            id="is-watched"
                                                            className={
                                                                e.isWatched
                                                                    ? "watched"
                                                                    : "not-watched"
                                                            }
                                                        >
                                                            {e.isWatched
                                                                ? "Watched"
                                                                : "Not-Watched"}
                                                        </div>
                                                        {isMobile && (
                                                            <div className="download-link">
                                                                <a
                                                                    href={
                                                                        e.dlLink
                                                                    }
                                                                    target="_blank"
                                                                    rel="noreferrer"
                                                                >
                                                                    Download
                                                                </a>
                                                            </div>
                                                        )}
                                                    </div>
                                                );
                                            })}
                                        </TabPanel>
                                    </div>
                                );
                            })}
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Home;

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`vertical-tabpanel-${index}`}
            aria-labelledby={`vertical-tab-${index}`}
            {...other}
        >
            {value === index && <Box p={3}>{children}</Box>}
        </div>
    );
}

function a11yProps(index) {
    return {
        id: `vertical-tab-${index}`,
        "aria-controls": `vertical-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: "#2d2d2d",
        display: "flex",
        height: "500px",
        width: "90%",
        borderRadius: "15px",
        padding: "80px 0",
        filter: "drop-shadow(2px 15px 15px rgb(17, 17, 17))",
        ["@media (max-width:970px)"]: {
            width: "95%",
        },
        ["@media (max-width:540px)"]: {
            width: "95%",
        },
        ["@media (max-width:376px)"]: {
            width: "95%",
        },
    },
    tabs: {
        borderRight: `2px solid ${theme.palette.divider}`,
    },

    heading: {
        fontSize: theme.typography.pxToRem(20),
        fontWeight: theme.typography.fontWeightBold,
        textTransform: "capitalize",
    },

    fadeIn: {
        animation: "$fadeAnim 3s linear",
    },
    "@keyframes fadeAnim": {
        from: { opacity: 0 },
        to: { opacity: 1 },
    },
}));
